import React from 'react'
import Tag from '../Tag/Tag'
import './Project.scss'
import {PropTypes} from 'prop-types';
import ProjectLinks from './../ProjectLinks/ProjectLinks';

const Project = (props, context) => {
    const {theme: {colorPrimary, textAlternate, bgPrimary, textPrimary}} = context;

    // color
    // tags
    let tagsDOM = props.tags.map((t) =>
        <Tag key={t} name={t} textColor={textAlternate} backgroundColor={colorPrimary}/>
    );
    return (
        <div className="projectOuter">
            <div className={["project", props.id].join(' ')}
                 style={{backgroundColor: bgPrimary, color: textPrimary}}
            >
                <div className= "projectTitleSmall">
                    <span className="projectName" style={{color: colorPrimary}}>{props.name}</span>
                    <span style={{flexGrow: 100}}/>
                    <ProjectLinks {...props}/>
                </div>
                {props.date &&
                <div className="projectDate">
                    {props.date}
                </div>
                }
                <div className="projectDesc">{props.desc[0]}</div>
                <div className="projectTags">{tagsDOM}</div>
            </div>
        </div>
    )
}

Project.contextTypes = {
    theme: PropTypes.any
};

export default Project;