import React from 'react';
import PropTypes from 'prop-types';

import ScrollToNext from "./ScrollToNext/ScrollToNext";

const About = (props, context) => {
    const {
        theme: {colorPrimary, colorHighlight, bgPrimary, textPrimary}
    } = context;

    return (
        <div id="about" className="container" style={{backgroundColor: bgPrimary}}>
            <style jsx="true">
                {`
          .highlight {
            background-color: ${colorHighlight};
          }
          ::selection {
            background-color: ${colorHighlight};
          }
        `}
            </style>
            <div>
                <h1 style={{color: colorPrimary}}>About</h1>

                <div style={{color: textPrimary}}>

                    <p>I started my career as an <span className="highlight">Android developer</span>, responsible
                        for the complete development lifecycle of the mobile applications including designing user
                        interface, preparing content, programming and debugging.
                        I understood how crucial documentation, version control, and effective team communication are in terms of having a
                        sane workflow.
                    </p>

                    <p>I prefer to spend much time in learning new features, presently which is feeling confident
                        in both sides of the spectrum to develop Web Apps from back to front along with their mobile
                        counterparts. <span className="highlight">ReactJS</span> has become one of my favorite tools in development.</p>

                    <p>I would love to build best mobile experiences using Android, iOS and <span className="highlight">Progressive Web Apps (PWA)</span> for your team.
                    </p>
                    <p className="text-emoji" style={{color: colorPrimary}}>
                        \ (•◡•) /
                    </p>
                </div>
            </div>
            <ScrollToNext pageSelector="#contact"/>
        </div>
    );
};

About.contextTypes = {
    theme: PropTypes.any
};

export default About;
