import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {toElement as scrollToElement} from '../../libs/scroll';

import './style.scss'
import {Helmet} from "react-helmet";
import Scrollspy from 'react-scrollspy';

import {FaMagic} from "react-icons/fa/";


class Nav extends Component {
    constructor(props) {
        super(props);
        this.handleScroll = this.handleScroll.bind(this);
        this.state = {
            isSticky: false
        };
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll() {

        // if (window.pageYOffset > window.nav.offsetTop) {
        //     this.setState({
        //         isSticky: true
        //     });
        // } else {
        //     this.setState({
        //         isSticky: false
        //     });
        // }
    }

    scrollToPage(pageSelector) {
        const nextPage = document.querySelector(pageSelector);
        scrollToElement(nextPage);
    }

    render() {
        const {theme: {colorPrimary, bgPrimary, navAlpha}, switchTheme} = this.context;

        const stickyClass = this.state.isSticky ? 'sticky' : '';
        const stickyStyles = this.state.isSticky
            ? {backgroundColor: navAlpha, color: colorPrimary}
            : {backgroundColor: bgPrimary, color: colorPrimary};
        return (
            <div>
                <Helmet
                    title="Nabil Mosharraf | Fullstack Dev"
                    meta={[
                        {name: 'description', content: 'Sample'},
                        {name: 'keywords', content: 'pwa, portfolio'},
                    ]}
                >
                    <html lang="en"/>
                </Helmet>
                <nav className={stickyClass} ref={(elem) => {
                    this.nav = elem;
                }} style={stickyStyles}>
                    <div className="magic-wand bounce-xy" onClick={(e) => switchTheme()}>
                        <FaMagic/>
                        <div className="magic-text">Color Me</div>
                    </div>
                    <style jsx="true">
                        {`
            .menu__item:hover {
              border-bottom: 2px solid ${colorPrimary};
            }
          `}
                    </style>
                    <div className="menu">
                        <Scrollspy
                            items={['about', 'contact', 'portfolio']}
                            currentClassName="active"
                            offset={-300}
                        >
                            {[
                                ['about', 'About'],
                                ['contact', 'Contact'],
                                ['portfolio', 'Portfolio']
                            ].map(entry => (

                                    <a
                                        key={entry[0]}
                                        className="menu__item"
                                        onClick={(e) => this.scrollToPage(`#${entry[0]}`)}
                                    >
                                        {entry[1]}
                                    </a>
                            ))}
                        </Scrollspy>
                    </div>
                </nav>
            </div>
        );
    }
}

Nav.contextTypes = {
    theme: PropTypes.any,
    switchTheme: PropTypes.func
};

export default Nav;
